// Animax   

import { memo, useCallback, useEffect, useLayoutEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { Menu } from "../modules/Menu";

export const TopArea = memo(({chgPageFn})=>{
    const [isScrollTop, setIsScrollTop] = useState(0);

    const checkTop = () => {
        // console.log(window.scrollY);
      if (window.scrollY >= 70) {
        setIsScrollTop(1);
      }
      else{
        setIsScrollTop(0);
      }
    };
    
    useEffect(() => {
      window.addEventListener("scroll", checkTop);
      return () => {
        window.removeEventListener("scroll", checkTop);
      };
    });

    return(
        <>
            {/* 1.  */}
            <header className={"top_area"+(isScrollTop?" fixed":"")}>
                {/* 1-1. */}
                <h1 className="logo">
                    <Link to="/main">
                        <img src="./images/logo.png" alt="Animax" />
                    </Link>
                </h1>
                {/* 1-2.GNB */}
                <nav className="gnb_box">
                    <ul>
                        <Menu cat="gnb"/>
                    </ul>
                </nav>
                {/* 1-3.UTIL */}
                <div className="util_box">
                    <ul>
                        <li className="search">
                            <a href="#"><span className="sr-only"></span></a>
                        </li>
                        <Menu cat="util"/>
                    </ul>
                </div>
            </header>
        </>
    );
})////// TopArea  ////