import React from 'react';

export function BestTagBtn({ filterItem, tagTotalItem, tagBtnAct }) {
  return (
    <>
      {tagTotalItem.map((v, i) => {
        return i === 0 ? (
          <button
            active={v === 0 && tagBtnAct === ''}
            key={i}
            onClick={() => {
              filterItem('');
            }}
            className={tagBtnAct === v ? 'on' : ''}
          >
            
          </button>
        ) : (
          <button
            active={v !== 0 && tagBtnAct === v}
            key={i}
            onClick={() => filterItem(v)}
            className={tagBtnAct === v ? 'on' : ''}
          >
            {v}
          </button>
        );
      })}
    </>
  );
}
