// Animax  

import { ThumList } from "./ThumList";
import { Promotion } from "./Promotion";
import { Today } from "./Today";
import { TopBanner } from "./TopBanner";
import { Vod } from "../modules/Vod";
import { Best } from "./Best";
import { useState } from "react";
import { BestTag } from "./BestTag";
import useModals from "../modules/useModals";
import { modals } from "../modules/Modals";
import { GoodVod } from "./GoodVod";

// Animax TV 
import { VideoListData } from "../data/video_list";
import { infoVodData } from "../data/infoVod_list";

export function Main(props) {
  // video  
  let VodListData = VideoListData;
  VodListData = [...VodListData];

  // video ,  
  let ifVodData = infoVodData;
  ifVodData = [...infoVodData];

  const [objInfo, setObjInfo] = useState({});

  const getInfo = (x) => {
    setObjInfo(x);
  };

  const { openModal } = useModals();
  const onClickVodHandler = (e) => {
    e.preventDefault();
    openModal(modals.infoModal, {});
  };
  const onClickVideoHandler = (e) => {
    e.preventDefault();
    openModal(modals.videoModal,{});
  };
  return (
    <>
      {/* 2-1. TOP  */}
      <TopBanner cat={"main"} />
      {/* 2-2. LIST  */}
      <ThumList cat="list" />
      {/* 2-3.  */}
      <section className="content_wrap">
        <h2 className="sr-only"> </h2>
        {/* 2.3.1   */}
        <Promotion cat="sub" />
        {/* 2.3.2    */}
        <Today cat="today" item={getInfo} />
        {/* 2.3.2    */}
        <Best cat="best" item={getInfo} VodListData={VodListData} />
        {/* 2.3.3  vod */}
        <GoodVod item={getInfo} VodListData={VodListData} ifVodData={ifVodData} onClickVodHandler={onClickVodHandler} onClickVideoHandler={onClickVideoHandler}/>
        {/* 2.3.4    */}
        <BestTag cat="tag" item={getInfo} VodListData={VodListData} />
      </section>
      {/* 3. VOD  */}
      <Vod item={objInfo} onClickVodHandler={onClickVodHandler} onClickVideoHandler={onClickVideoHandler}/>
    </>
  );
}
